import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import './layers.scss'
import classNames from 'classnames'
import { Link } from "react-router-dom";

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { getLayers } from '../../services/backend'
import Spinner from '../../components/spinner'

export default function Layers (props) {

  const [tableData, setTableData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    setIsLoading(true);
    getLayers().then((response) => {
      setTableData(response.data);
      setIsLoading(false);
    })
  }, []);

  return (
    <Paper className="Paper-main Layers">
      <h2>Layers</h2>
      {isLoading && <Spinner />}
      {!isLoading &&
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Key</TableCell>
            <TableCell>Title</TableCell>
            <TableCell>Z-index</TableCell>
            <TableCell>Colors</TableCell>
            <TableCell>Images quantity</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {tableData.map(row => (
            <TableRow className="table-row" hover={true} key={row._id}
                      onClick={() => props.history.push('layers/' + row._id)}>
              <TableCell component="th" scope="row">
                {row.key}
              </TableCell>
              <TableCell component="th" scope="row">
                {row.title}
              </TableCell>
              <TableCell component="th" scope="row">
                {row.zIndex}
              </TableCell>
              <TableCell component="th" scope="row">
                {(row.colors && row.colors.length) ?
                  row.colors.map(color => (
                    <div className="color" style={{background: color}}></div>
                  )) :
                  'No colors'
                }
              </TableCell>
              <TableCell component="th" scope="row">
                {row.images}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      }
    </Paper>
  )
}
