import "react-tabs/style/react-tabs.css";
import './App.scss';
import { connect } from 'react-redux'


export default class App extends React.Component {

  render() {
    return (
      <div className="App">
        <Router>
          <Route path="/avatar-platform-admin/" exact component={Layers} />
          <Route path="/avatar-platform-admin/layers/:id" component={LayerItems} />
        </Router>
      </div>
    );
  }
}

