import axios from 'axios';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:3001/';
console.log(API_URL);

const axiosInstance = axios.create({
  headers: {
    'Content-Type': 'application/json',
  }
});

const axiosFilesInstance = axios.create({
  headers: {
    'Content-Type': 'multipart/form-data',
  }
});

export function getLayers() {
  return axiosInstance.get(API_URL + 'admin/layers');
}


export function getLayerItems(id) {
  return axiosInstance.get(API_URL + 'admin/layers/' + id);
}

export function postLayerItemsImages(id, fileToSend) {
  let data = new FormData();
  data.append('image', fileToSend);
  data.append('name', 'image');

  return axiosFilesInstance.post(API_URL + 'admin/layers/' + id + '/images', data);
}

export function deleteLayerItemImage(id, imageId) {
  return axiosInstance.delete(API_URL + 'admin/layers/' + id + '/images/' + imageId);
}

export function patchLayerItem(id, data) {
  return axiosInstance.patch(API_URL + 'admin/layers/' + id, data);
}

export function postLayerItemColor(id, color) {
  return axiosInstance.post(API_URL + 'admin/layers/' + id + '/colors', {color});
}

export function deleteLayerItemColor(id, color) {
  color = color.replace('#', '');
  return axiosInstance.delete(API_URL + 'admin/layers/' + id + '/colors/' + color);
}

export function saveImage(data) {
  //return axiosInstance.get('http://localhost:3001/' + 'save-image',);
  return axios({
    url: API_URL + 'save-image',
    method: 'GET',
    responseType: 'blob', // important,
    params: { data }
  })
}
