import React, {useState, useEffect, createRef} from 'react';
import {connect} from 'react-redux';
import './colors.scss'
import Paper from '@material-ui/core/Paper';
import {postLayerItemColor, deleteLayerItemColor} from '../../../services/backend'
import { Link } from "react-router-dom";
import Spinner from "../../../components/spinner";
import ColorPicker from '../../../components/material-ui-color-picker'
import {Formik} from "formik";
import Button from '@material-ui/core/Button';


export default function Colors (props) {

  const [newColor, setNewColor] = useState('#ffffff');

  return (
    <Paper className="Paper-main Colors">
      <h2>Colors</h2>
      {props.data && props.data.colors && props.data.colors.map(color => (
       <div className="color" style={{background: color}} onClick={() => {
         deleteLayerItemColor(props.id, color).then(response => {
           props.setImagesData(response.data[0])
         })
       }}></div>
      ))}
      <Formik
        enableReinitialize
        initialValues={{color: '#ffffff'}}
        onSubmit={(values, { setSubmitting }) => {
          postLayerItemColor(props.id, newColor).then(response => {
            setSubmitting(false);
            props.setImagesData(response.data[0])
          })
        }}
      >
        {props => {
          const {
            values,
            errors,
            touched,
            isSubmitting,
            handleChange,
            handleSubmit,
          } = props;
          return (
            <form onSubmit={handleSubmit}>
              <p>
                <ColorPicker
                  label="Add new color"
                  id='color'
                  name='color'
                  value={newColor}
                  onChange={val => setNewColor(val)}
                />
              </p>
              <p>
                <Button variant="contained" type="submit" color="primary" disabled={isSubmitting}>
                  Submit
                </Button>
              </p>
            </form>
          )
        }}
      </Formik>
    </Paper>
  )
}
