import React, {useState, useEffect, createRef} from 'react';
import {connect} from 'react-redux';
import './layer-items.scss'
import Paper from '@material-ui/core/Paper';
import { getLayerItems, postLayerItemsImages, deleteLayerItemImage } from '../../services/backend'
import Dropzone from 'react-dropzone'
import { Link } from "react-router-dom";
import Spinner from "../../components/spinner";
import Colors from './colors';
import Images from './images'
import Form from './form'

export default function LayerItems (props) {

  const [imagesData, setImagesData] = useState({});
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    setIsLoading(true);
    getLayerItems(props.match.params.id).then((response) => {
      setImagesData(response.data[0]);
      setIsLoading(false);
    })
  }, [props.match.params]);

  function onImageSubmit(acceptedFiles) {
    let fileToSend = acceptedFiles[0];
    setIsLoading(true);
    postLayerItemsImages(props.match.params.id, fileToSend)
      .then((response) => {
        setIsLoading(false);
        setImagesData(response.data[0]);
      })
  }

  function onImageDelete(imageId) {
    setIsLoading(true);
    deleteLayerItemImage(props.match.params.id, imageId)
      .then((response) => {
        setIsLoading(false);
        setImagesData(response.data[0]);
      })
  }

  return (
    <div className='LayerItems'>
      <Link to='../'>Back to Layers</Link>
      <Form
        id={props.match.params.id}
        data={imagesData}
      />
      <Colors
        id={props.match.params.id}
        data={imagesData}
        setImagesData={setImagesData}
      />
      <Images
        id={props.match.params.id}
        data={imagesData}
        onImageDelete={onImageDelete}
        onImageSubmit={onImageSubmit}
        isLoading={isLoading}
      />
    </div>
  )
}
